import React from 'react';
import classes from './Home.module.css';
import banner from '../../images/banner.png';
import MemberCard from '../ui/MemberCard';
import FaqCard from '../ui/FaqCard';
import team_adam from '../../images/team/adam.png';
import team_borgar from '../../images/team/borgar.png';
import team_robert from '../../images/team/robert.png';
import team_hordur from '../../images/team/hordur.png';
import discord_logo from '../../images/discord_logo_pixel.png';
import twitter_logo from '../../images/twitter_logo_pixel.png';

import * as faqs from '../data/faq.json';

function Home() {

  const borgar_desc = 'Mr. Burger man loves his mom and likes to cuddle his 5 cats';
  const robert_desc = 'Dr. Lobster dude loves to cook exquisite gordon bleu recipes and read books about butter';
  const adam_desc = 'Mr. DamDam is known for his unsurpassable knowldge on money stuff, he also owns a mint condition $2 bill';
  const hordur_desc = 'Sr. Phurb is one sick fuck. We won\'t say any more about him.';

  const faqItems = faqs.data.map(item => (
    <FaqCard title={item.title} description={item.description} bullet={item.bullet} />
  ))

  return (
    <div className={classes.container}>
      <div className={classes['container-1']}>
        <img className={classes['container-1-banner']} src={banner} />
        <p className={classes['container-1-title']}>KAMIKAZE KOALAS</p>
        <div className={classes['container-1-social']}>
          <a href="http://www.twitter.com" target="_blank" rel="noopener noreferrer" ><img src={twitter_logo}/></a>
          <a href="http://www.discord.com" target="_blank" rel="noopener noreferrer" ><img src={discord_logo}/></a>
        </div>
      </div>
      <div className={classes['container-2']}>
        <p className={classes['section-title']}>FAQ Stuff</p>
        {faqItems}
      </div>
      <div className={classes['container-3']}>
        <p className={classes['section-title']}>TEAM KAMIKAZE</p>
        <div className={classes['container-3-team']}>
          <MemberCard url={team_adam} description={adam_desc} title='Mr. DamDam' />
          <MemberCard url={team_robert} description={robert_desc} title='Dr. Lobster' />
          <MemberCard url={team_borgar} description={borgar_desc} title='Mr. Burger man' />
          <MemberCard url={team_hordur} description={hordur_desc} title='Sr. Phurb' />
        </div>
      </div>
    </div>
  )
}

export default Home;
